@charset "UTF-8";

html {
  font-size: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}
body {
  background-color: #fff;
  color: #333;
  font-family:  "Noto Serif JP", serif;
  overflow-x: hidden;
}
img {
  max-width: 100%;
  object-fit: cover;
  vertical-align: bottom;
}
li {
  list-style: none;
}
a {
  color: #333;
  text-decoration: none;
  transition: all 0.3s ease;
}
main {
  margin-top: 72px;
}

.hover-line {
  position: relative;
}
.hover-line::after {
  position: absolute;
  left: 0;
  content: '';
  width: 100%;
  height: 1px;
  background: #333;
  bottom: -5px;
  transform: scale(0, 1);
  transform-origin: center top;
  transition: transform 0.5s;
}

.wrapper {
  max-width: 1120px;
  padding: 0 40px;
  margin: 0 auto;
}
.c-wrapper {
  max-width: 920px;
  padding: 0 40px;
  margin: 0 auto;
}

.contents-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 80px;
  margin-bottom: 80px;
  gap: 10px;
  position: relative;
  z-index: 11;
}
.contents-title .en {
  display: block;
  font-size: 56px;
  font-weight: bold;
  letter-spacing: 0.1em;
  line-height: 1;
  margin-right: 16px;
  font-family: "Playfair Display", serif;
}
.contents-title .ja {
  display: block;
  font-size: 18px;
  font-weight: normal;
  letter-spacing: 0.08em;
  line-height: 1;
  margin-bottom: 10px;
  color: #333;
  
}
.child_mv .contents-title .ja {
 color: #fff;
  
}
.contents-subtitle {
  display: inline-block;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 0.1em;
  margin-bottom: 40px;
  position: relative;
}
.contents-subtitle::before {
  content: "";
  width: 80px;
  height: 1px;
  background-color: #333;
  position: absolute;
  top: 14px;
  right: -100px;
}
.contents-subtitle span {
  font-size: 12px;
  line-height: 1.6;
  display: block;
  font-weight: 700;
}
.main_btn {
  margin: 0 auto;
  width: fit-content;
  max-width: 400px;
  width: 80%;
  position: relative;
  transition: all 0.3s ease;
}
.main_btn::before {
  content: '';
  width: 100%;
  height: 100%;
  border: 3px solid #d150eb;
  position: absolute;
  top: -8px;
  left: -8px;
  z-index: 11;
  pointer-events:none;
  transition: all 0.3s ease;
  
}
.main_btn::after {
  content: '';
  width: 100%;
  height: 120%;
  background: rgba(255,255,255, 0.5);
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 11;
  pointer-events:none;
  transform: translate(-200% ,0%) skew(-30deg);
  transition: all 0.3s ease;
}
.main_btn:hover::after {
  transform: translate(200% ,0%) skew(-30deg);
}
.main_btn a {
  
  display: block;
  padding: 20px 0;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  border: 2px solid #0F2540;
  background-color: #0F2540;
  transition: all 0.3s ease;
  color: #fff;
}

.main_btn a:hover {
  background-color: #0F2540;
  color: #fff;
}
.main_btn:hover::before {
 top: 0;
 left: 0;
}

.button_mt {
  margin-top: 120px;
}

.child_mv {
  width: 100vw;
  height: 350px;
  margin-right:calc(50% - 50vw);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -80px;
  margin-bottom: 20px;
  position: relative;
  
}
.child_mv::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.child_mv.skills_bg {
  background-image: url(../img/emile-perron-xrVDYZRGdw4-unsplash.jpg);
  background-size: cover;
  background-position: center;
  filter: grayscale(0);

  
}
.child_mv.about_bg {
  background-image: url(../img/joshua-aragon-BMnhuwFYr7w-unsplash.jpg);
  background-size: cover;
  background-position: center;
  filter: grayscale(0);

  
}
.child_mv.contact_bg {
  background-image: url(../img/nick-morrison-FHnnjk1Yj7Y-unsplash.jpg);
  background-size: cover;
  background-position: center;
  filter: grayscale(0);

  
}
.child_mv.works_bg {
  background-image: url(../img/christopher-gower-m_HRfLhgABo-unsplash.jpg);
  background-size: cover;
  background-position: center;
  filter: grayscale(0);

  
}
.breadCrumb {
  display: flex;
  gap: 16px;
  font-size: 14px;
  margin-bottom: 100px;
  margin-left: 4px;
}
.breadCrumb li {
  position: relative;
}
.breadCrumb li::before {
  content: "";
  width: 6px;
  height: 6px;
  border-top: 1px solid #333;
  border-right: 1px solid #333;
  position: absolute;
  top: 50%;
  left: -14px;
  transform: translateY(-50%) rotate(45deg);
}
.breadCrumb li:first-child:before {
  content: none;
}

.current {
  text-decoration: underline;
}

.top_button {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: fixed;
  left: 3%;
  bottom: 100px;
  z-index: 211;
  
 

}

.top_button :hover {
  animation: bund 0.5s ease-in-out 1;
}

@keyframes bund {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}

.top_bg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #0F2540;
  position: relative;
 z-index: -1;
  
}
.top_bg::before {
  content: '';
  width: 20px;
  height: 20px;
  
  background-color: #fff;
 clip-path: polygon(50% 0%, 100% 100%, 0% 100%);
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 999;
 
}

.top_button span {
  font-size: 14px;
  font-weight: 700;
  color: #0F2540;
  text-align: center;
  
}

/*-------------------------------------------
Header
-------------------------------------------*/
#header {
  width: 100%;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  padding: 20px 40px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
}
#header.header_underLine {
  position: relative;
}
#header.header_underLine::before {
  content: '';
  border-bottom: solid 1px #ccc;
 
  width:92% ;
  height: 72px;
  position: absolute;
  top: 0;
  right: 0;
  
}
#header.childHeader.header_underLine::before  {
  height: 105px;
}

#header.header_active {
  background-color: #fff;
  
  
}

#header.header_active::before {
  content: '';
  width: 95%;
  height: 82px;
  border-bottom: solid 1px #ccc;
  position: absolute;
  top: 0;
  right: 0;
}

#header .logo {
  width: 100%;
  max-width: 230px;
  line-height: 0;
  position: relative;
  z-index: 222;
  margin-top: 250px;
  margin-left: 100px;
  transform: rotate(90deg);
  transform-origin: left top;
  transition: all .4s;
}
#header .logo a {
  display: block;
  
}
#header .logo img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
#header .navi .menu {
  display: flex;
  align-items: center;
}
#header .navi .menu li {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.05em;
  margin-left: 40px;
  position: relative;
  color: #0F2540;

}
#header .navi .menu li span {
  font-size: 12px;
  font-weight: 400;
  
  line-height: 1.6;
}

#header .navi .menu li::before {
  content: "";
  width: 1px;
  height: 22px;
  background-color: #0F2540;
  position: absolute;
  bottom: 0;
  right: -20px;
  transform: rotate(30deg);
  z-index: 11;
} 

#header .navi .menu li::after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #fff;
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%) scaleX(0);
 z-index: 11;
 transition: all .3s ease;
}

#header .navi .menu li:hover::after {
  transform: translateX(-50%) scaleX(1);
}

#header .navi .menu li:last-of-type::before {
  content: none;
}

#header .navi .menu li  a {
  color: inherit;
}

#header .navi .menu .linkText_active {
  color: #333;
}

#header .navi .menu .linkText_active::before {
  background-color: #333;
}
#header .navi .menu li .hover-line:hover::after {
  transform: scale(1, 1);
}
#header .navi .menu li.linkText_active:hover::after{
  transform: translateX(-50%) scaleX(0);
}
#header .navi .menu .linkText_active::before {
  content: '';
  width: 38px;
  height: 4px;
  border-radius: 45px;
  background-color: #0F2540;
  position: absolute;
  bottom: -11px;
  left: 50%;
  transform: translateX(-50%);
  transition: all .3s ease-out;
  opacity: 0;
}
#header .navi .menu .linkText_active.marker::before {
 
  opacity: 1;
}
#header.header_active .logo {
  transform: rotate(0);
  margin-top: 20px;
  margin-left: 0px;
 
}
#header.childHeader {
  position: static;
  background-color: #fff;
  height: 120px;
  padding-bottom: 20px;
}
#header.childHeader .navi .menu  li a {
  color: #333 ;
}

#header.childHeader .navi .menu li::before {
 
  background-color: #333;

} 
#header.childHeader .logo {
  transform: rotate(0);
  margin-top: 20px;
  margin-left: 0px;
}
#header.childHeader {
  align-items: center;
  padding: 20px 40px;
}


/*-------------------------------------------
Fv
-------------------------------------------*/
.fv {
 
  

  height: calc(100vh - 120px);
  background-size: cover;
  background-color: #fff;
  margin: 0 auto;
  position: fixed;
  top: 120px;
  left: 0;
  right: 0;
  width: 100vw;
  z-index: -3;
}



.fv_slide-image {
  object-position: center;
  background-repeat: no-repeat;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: calc(100vh - 120px);
  opacity: 0;
  animation: slider-1 24s linear infinite;
}
.fv_slide-image img {
  width: 100%;
  height: 100%;
}

.fv_slide-image:nth-child(1){
  animation-delay: -2s;
}

.fv_slide-image:nth-child(2){
  animation-delay: 6s;
}

.fv_slide-image:nth-child(3){
  animation-delay: 14s;
}

@keyframes slider-1 {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  4.16% {
    opacity: 1;
  }
  33.33% {
    opacity: 1;
  }
  41.66% {
    opacity: 0;
   
  }
  100% {
  opacity: 0;
  }
}

.fv_title {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  z-index: 11;
}
.fv_slide-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  
}
.fv .site-title {
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  letter-spacing: 0.1em;
  line-height: 1.6;
  margin: 0 0  16px 10%;
  color: #fff;
  
  
  
 
}

.fv .site-title span {
  display: block;
  margin-top: 10px;
  line-height: 1;
  background-color: #0F2540;
  padding: 10px 20px;
  width: fit-content;
  white-space: nowrap;

}
.fv .site-subtitle {
  text-align: center;
  font-size: 14px;
  letter-spacing: 0.1em;
  margin: 30px 0 12px 0;
  color: #fff;
}

.mv_bg {
  height: 120px;
  width: 80vw;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #0F2540;
  z-index: -1;
}

/*-------------------------------------------
Works
-------------------------------------------*/
.works {
  padding-bottom: 80px;
