/*-------------------------------------------
Link list
-------------------------------------------*/
.link-list {
  display: flex;
  justify-content: space-between;
}
.link-list li {
  width: 50%;
  height: 320px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #dfdfdf;
  border-top: solid 1px #7f7f7f;
}
.link-list li:first-child {
  border-right: solid 1px #7f7f7f;
}
.link-list li a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.link-list li a .title {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.link-list li a .title::before {
  content: "";
  width: 16px;
  height: 15px;
  background-image: url(../img/icon-arrow-right.svg);
  background-size: contain;
  position: absolute;
  top: 8px;
  right: -35px;
}
.link-list li a:hover {
  opacity: 0.7;
}
.link-list li a:hover .hover-line::after {
  transform: scale(1, 1);
}
.link-list li a .title .en {
  display: block;
  font-size: 32px;
  font-weight: bold;
  letter-spacing: 0.1em;
  line-height: 1;
  margin-right: 16px;
}
.link-list li a .title .ja {
  display: block;
  font-size: 12px;
  font-weight: normal;
  letter-spacing: 0.05em;
  line-height: 1;
  margin-top: 10px;
}

.LinkButton {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  position: fixed;
  top: 50%;
  left: 20px;
  right: 20px;
  transform: translateY(-50%);
}
.LinkButton li a{
  font-size: 14px;
  font-weight: 400;
  writing-mode: vertical-lr;
  position: relative;
}

.prev_button a::before {
  content: "";
  width: 60px;
  height: 40px;
  background-image: url(../img/arrow_Btn.svg);
  background-size: contain;
  transform: translateX(-50%);
  position: absolute;
  bottom: -40px;
  left: -20%;
  
}
.prev_button a:hover::before {
  animation: prev_buttonAnimation 1s ease-in-out;
}

@media screen and (max-width: 992px) {

  .LinkButton {
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    position: absolute;
    top: unset;
    bottom: 10px;
    left: 20px;
    right: 20px;
    transform: translateY(0);
  }
  .LinkButton li a{
  
    writing-mode:horizontal-tb;
    position: relative;
  }


}


@keyframes prev_buttonAnimation {
  0% {
    transform: translateX(-50%);
  }
  50% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(-50%);
  }
}


.next_button a::before {
  content: "";
  width: 60px;
  height: 40px;
  background-image: url(../img/arrow_Btn.svg);
  background-size: contain;
  transform: translateX(-50%) rotate(180deg);
  position: absolute;
  top: -40px;
  left: 110%;
}

.next_button a:hover::before {
  animation: next_buttonAnimation 1s ease-in-out;
}


@keyframes next_buttonAnimation {
  0% {
    transform: translateX(-50%) rotate(180deg);
  }
  50% {
    transform: translateX(-40%) rotate(180deg);
  }
  100% {
    transform: translateX(-50%) rotate(180deg);
  }
}


