.scroll_down {
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  color: #333;
  position: fixed;
  bottom: 7.4%;
  left: 50%;
  z-index: 11;
}

.scroll_down::before {
  content: '';
  width: 1px;
  height: 112px;
  background-color: #333;
  position: absolute;
  left: 50%;
  bottom: -120px;
  transform: scaleY(0);
  animation: scroll_animetion 1.3s ease-in-out infinite;
}

.scroll_right {
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  color: #333;
  position: absolute;
  bottom: 14.4%;
  left: 50%;
  z-index: 11;
}

.scroll_right::before {
  content: '';
  width: 112px;
  height: 1px;
  background-color: #333;
  position: absolute;
  right: -40px;
  bottom: 50%;
  transform: scaleX(0);
  animation: scroll_right_animetion 1.3s ease-in-out infinite;
}

  @keyframes scroll_animetion{
            0% {
                transform: scaleY(0);
                transform-origin: top;
            }

            50% {
                transform: scaleY(1);
                transform-origin: top;
            }
            51% {
                transform: scaleY(1);
                transform-origin: bottom;
            }
            
            100% {
                transform: scaleY(0);
                transform-origin: bottom;
            }
          }

          @keyframes scroll_right_animetion{
            0% {
                transform: scaleX(0);
                transform-origin: right;
            }

            50% {
                transform: scaleX(-1);
                transform-origin: right;
            }
            51% {
                transform: scaleX(-1);
                transform-origin: right;
            }
            
            100% {
                transform: scaleX(0);
                transform-origin: right;
            }
          }


