  }
  .project .detail-list .item {
    flex-direction: column;
    font-size: 14px;
    padding: 32px 0;
  }
  .project .detail-list .item dt {
    width: 100%;
    padding: 0;
    margin-bottom: 8px;
  }
  .project .detail-list .item dd {
    width: 100%;
    padding: 0;
  }
