  .skills .skills-list {
    flex-direction: column;
    font-size: 14px;
  }
  .skills .skills-list dt {
    width: 100%;
    border-bottom: none;
    padding: 32px 0 0;
    margin-bottom: 24px;
  }
  .skills .skills-list dd {
    width: 100%;
    padding: 0 0 32px;
  }
  .skills .skills-list dd .item .detail {
    width: 25%;
    margin-right: 3%;
  }
  .skills .skills-list dd .item .note {
